      },//
      handleClick(tab, event) {
        if(tab.name=="1") this.tabs=true;
        else this.tabs=false
      },//
      toDetail(data){
//          console.log(data);
          this.pro_id=data.project_id;
        this.dialogVisiblePro=true;
      },//
      addFollow(){
        this.dialogFollow=true;
      },//
      closeFollow(msg){
        this.dialogFollow=msg;
        this.getEnjoyProjects();
        this.getEchartData();
      },//
      dialogVisiblechangeIn(msg){
        this.dialogVisiblePro=msg;
//      this.dialogPreviewVisible=true;
      },//
      dialogPrechange(msg){
        this.dialogPushVisible=msg;
        this.dialogPushVisibleComplete=msg;
        this.dialogPreviewVisible=msg;
      },//
      setProjectList(data){
        let arr = new Array;
        for(let i=0; i<data.length; i++){
          let obj = new Object;
          obj.pro_intro=data[i].pro_intro || "";//
          obj.is_exclusive=data[i].is_exclusive;///
          obj.pro_industry=this.setIndustry(data[i].pro_industry) || [];//
          obj.pro_scale =data[i].pro_scale.scale_money || '-';//

          obj.pro_area =data[i].pro_area.area_title || '-';//
          obj.pro_stage =data[i].pro_stage.stage_name || '-';//
          obj.pro_finance_stock_after =data[i].pro_finance_stock_after || '-';//
          arr.push(obj);
        }
        return arr;
      },//
      setIndustry(data){
        let arr = new Array;
        for(let i=0; i<data.length; i++){
          arr.push(data[i].industry_name)
        }
        return arr;
      },//
      /**/
      getProjectList(page){
        var getProjectList = new Promise((resolve, reject)=>{
          //
          this.loading=true;
          this.getPra.user_id=this.contacts.user_id;
//      this.getPra.user_id="2rzyz5vp";
          this.currentPage=page;
          this.getPra.page=page;
          this.$http.post(this.URL.getProjectList,this.getPra)
            .then(res=>{
              if(res.data.status_code==2000000) {
                let data = res.data.data;
                this.projectListsAll = this.setProjectList(data);
                this.projectListsSmall = this.setProjectList(data).slice(0, 2);
                if (this.listShow) this.projectLists = this.projectListsAll.slice(0);
                else this.projectLists = this.projectListsSmall.slice(0);
                this.totalData = res.data.count;
              }
              this.loading = false;
            })
            .catch(err=>{
              this.$tool.console(err,2);
              this.loading1=false;
              this.$tool.error("");
            })
          resolve(4);
        });
        return getProjectList;

      },//
      setTag(arr){
        let newArr = new Array;
        arr.forEach((x)=> {
          newArr.push(x.tag_id);
        });
        return newArr;
      },//
      setProjectCase(arr){
        let newArr = new Array;
        arr.forEach((x)=> {
          let obj = new Object;
          obj.case_deal_time=this.$tool.formatDateTime(x.case_deal_time);
          obj.case_stage_name=x.case_stage_name;
          obj.case_name=x.case_name;
          obj.case_money=x.case_money;
          obj.has_many_industry=this.$tool.setTagToString(x.has_many_industry,'industry_name');
          obj.has_one_city=x.has_one_city.area_title;
          newArr.push(obj);
        });
        return newArr;
      },//
      getOneUserInfo(){
        var getOneUserInfo = new Promise((resolve, reject)=>{
          //
          this.loading=true;
          this.$http.post(this.URL.getOneUserInfo,{card_id: this.contacts.card_id})
            .then(res => {
              let data = res.data.data;
//          this.$tool.console(this.$tool.getToObject(data));
              data.user_invest_industry = this.$tool.setTagToString(data.user_invest_industry,'industry_name');
              data.user_invest_stage = this.$tool.setTagToString(data.user_invest_stage,'stage_name');
              data.user_invest_scale = this.$tool.setTagToString(data.user_invest_scale,'scale_money');
              data.user_resource_find = this.$tool.setTagToString(data.user_resource_find,'resource_name');
              data.user_resource_give = this.$tool.setTagToString(data.user_resource_give,'resource_name');
              data.project_case = this.setProjectCase(data.project_case);
              data.user_email=data.user_email || '';
              data.user_company_name=data.user_company_name || '';
              data.user_brand=data.user_brand || '';
              data.user_company_career=data.user_company_career || '';
              data.user_mobile=data.user_mobile || '';
              data.user_intro=data.user_intro || '';
              data.user_avatar_txt=this.$tool.setUrlChange(data.user_avatar_url,data.user_real_name);
              if(data.user_invest_industry=='' && data.user_invest_stage=='' && data.user_invest_scale=='' && data.user_invest_desc==''){
                this.user_invest=false;//
              }else{
                this.user_invest=true;//
              }
              if(data.user_resource_give=='' && data.user_resource_find=='' && data.user_resource_desc==''){
                this.user_resource=false;//
              }else{
                this.user_resource=true;//
              }
              this.tagsValue = this.setTag(data.user_invest_tag);
              this.tags.changecont = this.setTag(data.user_invest_tag);
              this.contacts = data;
              console.log(data);
              this.loading = false;
            })
            .catch(err=>{
              this.$tool.console(err,2);
//              this.loading=false;
              this.$tool.error("");
            })
          resolve(3);
        });
        return getOneUserInfo;

      },//
      getWxProjectCategory(){
        var getWxProjectCategory = new Promise((resolve, reject)=>{
          //
          setTimeout(()=>{
            this.addTags = this.$global.data.tags_user;//()
//      this.tags.changecont = this.$global.data.tags_user;//2
            this.follow_schedule = this.$global.data.follow_schedule.slice(0);//
            this.follow_scheduleAll = this.$global.data.follow_schedule.slice(0);
            this.follow_scheduleAll.unshift({label:'', value:0});//
            resolve(2);
          },500)

        });
        return getWxProjectCategory;


      },//
      addChangeTag(e){
        let tagName = this.$tool.checkArr(e, this.addTags);
        if (tagName != undefined) {
          if(tagName.length>40){
            this.$tool.error("40");
            this.contacts.user_invest_tag.pop();
          }else {
            this.$http.post(this.URL.createCustomTag, {user_id: localStorage.user_id, type: 3, tag_name: tagName})
              .then(res => {
                let newState = {};
                newState.label = tagName;
                newState.value = res.data.tag_id;
                this.tags.changecont.push(newState);
              })
              .catch(err => {
                this.$tool.error("");
                this.$tool.console(err);
              })
          }

        }
      },//
      addTag(){
        this.loading=true;
        this.$tool.setTag(this.tagsValue,this.tags.changecont);
        this.$http.post(this.URL.setConnectTag, {user_id:localStorage.user_id,card_id: this.tags.card_id,tag: this.tagsValue})
        .then(res => {
          this.loading=false;
          this.$tool.success("");
          this.dialogVisibleTag = false;
          this.gettags_user();
          setTimeout(()=>{this.getOneUserInfo();},100);
        })
        .catch(err => {
          this.loading=false;
          this.$tool.error("");
          this.$tool.console(err);
          this.dialogVisibleTag = false;

        })
      },//
      gettags_user(){
        this.$http.post(this.URL.getWxProjectCategory, {user_id: localStorage.user_id})
        .then(res => {
          let data = res.data.data;
          this.addTags = this.$tool.getTags_pro(data.tags_user);//
          this.$global.func.getWxProjectCategory();
          this.getWxProjectCategory();
        })
      },//
      /**/
      getEchartData(){
        this.loading = true;
        var getEchartData = new Promise((resolve, reject)=>{
          //
          this.$http.post(this.URL.getEnjoyProjectsGroup,{user_id:localStorage.user_id,card_id:this.contacts.card_id})
            .then(res=>{
              if(res.data.status_code==2000000) {
                let data = res.data.data;
                this.chartData=data;
                this.eChart(data.going,data.hold,data.reject);
              }
              this.loading = false;
            })
            .catch(err=>{
              this.$tool.console(err,2);
              this.loading1=false;
              this.$tool.error("");
            })
            resolve(5);
        });
        return getEchartData;

      },//()
      getEnjoyProjects(){
        this.loading = true;
        var getEnjoyProjects = new Promise((resolve, reject)=>{
          //
          this.getConpro.user_id=localStorage.user_id;
//      this.getPra.user_id="2rzyz5vp";
          this.currentPage2=1;
          this.getConpro.card_id=this.contacts.card_id;
          this.getConpro.page=1;
          this.getConpro.schedule_id='';
          this.$http.post(this.URL.getEnjoyProjects,this.getConpro)
            .then(res=>{
              if(res.data.status_code==2000000) {
                let data = res.data.data;
                this.enjoyProjects=this.setEnjoyProject(data);
                this.totalData2 = res.data.count || 0;
                if(this.enjoyProjects.length==0) this.activeName='2';
              }
              this.loading = false;
            })
            .catch(err=>{
              this.$tool.console(err,2);
              this.$tool.error("");
            })
          resolve(7);
        });
        return getEnjoyProjects;

      },//
      setEnjoyProject(arr){
        let newArr = new Array;
        arr.forEach((x)=> {
          let obj = new Object;
          obj.follow_id=x.follow_id;
          obj.area=x.area.area_title;
          obj.industry=x.industry;
          obj.is_exclusive=x.is_exclusive;
          obj.match=x.match;
          obj.pro_finance_stock_after=x.pro_finance_stock_after;
          obj.pro_intro=x.pro_intro;
          obj.project_id=x.project_id;
          obj.scale=x.scale.scale_money;
          obj.schedule_id=x.schedule_id;
          obj.stage=x.stage.stage_name;
          obj.width=this.selectChange(x.schedule_id);
          newArr.push(obj);
        });
        return newArr;
      },//
      getIndex(index){
        this.scheduleIndex=index;
      },
      eChart(going,hold,reject){
        if(this.chartCheck) this.chart = this.$echart.init(document.getElementById('echart'));
        let option = {
          tooltip: {
            trigger: 'item',
            formatter: "{a} <br/>{b}: {c} ({d}%)"
          },
          legend: {
            orient: 'vertical',
            x: 'right',

            top:'30%',
            data:["","Hold","Rejcet"],
            textStyle:{
              fontSize:"16",
            }
          },
          series: [
            {
              name: '',
              type: 'pie',
              selectedMode: 'single',
              radius: ['70%','90%'],
              center: ['35%', '55%'],
              label: {
                normal: {
                  show: true,
                  position: 'center',
                  textStyle:{
                    fontSize:"16",
                  }
                },
                emphasis: {
                  show: true,
                  textStyle: {
                    fontSize: '20',
                    fontWeight: 'bold'
                  }
                }
              },
              data: [
                {value: going, name: ''},
                {value: hold, name: 'Hold'},
                {value: reject, name: 'Rejcet'},
              ],
              itemStyle:{
                normal:{
                  label:{
                    show: true,
                    formatter: '{b} : {c}'
                  },
                  labelLine :{show:true}
                }
              }
            }
          ]
        };
        this.chart.setOption(option);
        this.chartCheck=false;
      },//
      selectChange(e){
        let width = 0;
        switch (e){
          case 1:
            width=10;
            break;
          case 2:
            width=20;
            break;
          case 3:
            width=30;
            break;
          case 4:
            width=40;
            break;
          case 5:
            width=50;
            break;
          case 6:
            width=60;
            break;
          case 7:
            width=70;
            break;
          case 8:
            width=0;
            break;
          case 9:
            width=0;
            break;
          default:
            alert("")
            break;
        }
        let index = this.scheduleIndex;
        if(index!=-1) {
          this.enjoyProjects[index].width = width
          let follow_id = this.enjoyProjects[index].follow_id;
          let schedule_id = this.enjoyProjects[index].schedule_id;
          this.$http.post(this.URL.setEnjoyProjectSchedule, {
            user_id: localStorage.user_id,
            follow_id: follow_id,
            schedule_id: schedule_id
          })
          .then(res => {
            this.$tool.success("");
            this.scheduleIndex=-1;
            this.loading = false;
            this.getEchartData();
          })
          .catch(err => {
            this.$tool.console(err, 2);
            this.loading = false;
            this.$tool.error("");
          })
        }
        return width;
      },//
      selectSearch(e){
        this.loading=true;
        this.getConpro.schedule_id=e;
        this.getConpro.user_id=localStorage.user_id;
        this.currentPage2=1;
        this.getConpro.card_id=this.contacts.card_id;
        this.getConpro.page=1;
        this.$http.post(this.URL.getEnjoyProjects,this.getConpro)
        .then(res=>{
          if(res.data.data.length!=0){
            if(res.data.status_code==2000000) {
              let data = res.data.data;
              this.enjoyProjects=this.setEnjoyProject(data);
              this.totalData2 = res.data.count;
            }
          }else{
            this.enjoyProjects=[];
            this.totalData2 = 0;
          }
          this.loading = false;
        })
        .catch(err=>{
          this.$tool.console(err,2);
          this.loading=false;
          this.$tool.error("");
        })
      },//
      /*()*/
      getInvestorsMatchProjects(){
        var getInvestorsMatchProjects = new Promise((resolve, reject)=>{
          //
          this.getMatchPro.user_id=localStorage.user_id;
//      this.getPra.user_id="2rzyz5vp";
          this.currentPage3=1;
          this.getMatchPro.investor_id=this.contacts.investor_id;
          this.getMatchPro.card_id=this.contacts.card_id;
          this.getMatchPro.page=1;
          this.$http.post(this.URL.getInvestorsMatchProjects,this.getMatchPro)
            .then(res=>{
              if(res.data.status_code==2000000) {
                if(res.data.data){
                  let data = res.data.data;
                  this.matchProjects=this.setMatchProject(data);
//                  console.log(this.matchProjects);
                  this.totalData3 = res.data.count;

                }
              }
            })
            .catch(err=>{
              this.$tool.console(err,2);
              this.loading=false;
              this.$tool.error("");
            })
          resolve(6);
        });
        return getInvestorsMatchProjects;

      },//
      setMatchProject(arr){
        let newArr = new Array;
        arr.forEach((x)=> {
          let obj = new Object;
          obj.area=x.pro_area.area_title;
          obj.industry=x.pro_industry;
          obj.is_exclusive=x.is_exclusive;
          obj.match=x.match;
          obj.pro_finance_stock_after=x.pro_finance_stock_after;
          obj.pro_intro=x.pro_intro;
          obj.project_id=x.project_id;
          obj.scale=x.pro_scale.scale_money;
          obj.stage=x.pro_stage.stage_name;
          obj.is_follow=x.is_follow;
          newArr.push(obj);
        });
        return newArr;
      },//
      filterChangeCurrent2(page){
        this.loading=true;
        this.getMatchPro.user_id=localStorage.user_id;
//      this.getPra.user_id="2rzyz5vp";
        this.currentPage3=page;
        this.getMatchPro.investor_id=this.contacts.investor_id;
        this.getMatchPro.card_id=this.contacts.card_id;
        this.getMatchPro.page=page;
        this.$http.post(this.URL.getInvestorsMatchProjects,this.getMatchPro)
        .then(res=>{
          if(res.data.status_code==2000000) {
            if(res.data.data){
              let data = res.data.data;
              this.matchProjects=this.setMatchProject(data);
              this.totalData3 = res.data.count;
            }
          }
          this.loading = false;
        })
        .catch(err=>{
          this.$tool.console(err,2);
          this.loading=false;
          this.$tool.error("");
        })
      },//
      delMatchAction(data){
        let delData=new Object;
        delData.user_id=localStorage.user_id;
        delData.investor_id=this.contacts.investor_id;
        delData.project_id=data.project_id;
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.loading=true;
          this.$http.post(this.URL.exceptMatchAction,delData)
          .then(res=>{
            if(res.data.status_code==2000000) {
              this.$tool.success("")
              this.getInvestorsMatchProjects();
            }
            this.loading = false;
          })
          .catch(err=>{
            this.$tool.console(err,2);
            this.loading=false;
            this.$tool.error("");
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },//
      /**/
      getpushCount(){
        this.$http.post(this.URL.pushCount,{
          user_id: localStorage.user_id})
          .then(res=>{
            let data = res.data.data;
            this.pushCount=data.push_count.remain_times;
//          this.$tool.console(data.push_count);
          })
          .catch(err =>{
            this.$tool.console(err,2);
            this.loading=false;
          })
      },//
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.push();
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },//
      push(){
        if(this.pushCount!=0){
            let pushData=new Object;
            pushData.user_id= localStorage.user_id;
            pushData.card_id=this.contacts.card_id;
            pushData.email=this.pushData.email;
            pushData.title="";
            pushData.body=this.pushData.body;
            pushData.project_ids=new Array;
            pushData.project_ids.push(this.pushData.project_id);
            this.$http.post(this.URL.pushUser, pushData)
              .then(res => {
                if(res.data.status_code===2000000){
                  let data=res.data.data;
                  this.$tool.success("");
                  this.getpushCount();
                  this.getInvestorsMatchProjects();
                  this.dialogPushVisible = false;
                }else{
                  this.$tool.error(res.data.error_msg)
                }
              })
              .catch(err => {
                this.$tool.console(err);
                this.$tool.success("");
                this.dialogPushVisible = false;
              })
          }else{
          this.$tool.warning("")
        }
      },//
      getNewWxPro(){
        var getNewPro = new Promise((resolve, reject)=>{
          //
          this.$global.func.getWxProjectCategory();
          resolve(1);
        });
        return getNewPro;
      },//
    },
    created(){
      this.loading=true;
      this.getUserId();
      this.getpushCount();
      if(this.contacts.user_id!=0) this.getProjectList(1)
      else this.projectLists=[];
      this.getNewWxPro()
        .then((data)=>{
          return this.getWxProjectCategory();
        })
        .then((data)=>{
          return this.getOneUserInfo();
        })
        .then((data)=>{
          return this.getEchartData();
        })
        .then((data)=>{
          return this.getInvestorsMatchProjects();
        })
        .then((data)=>{
          this.loading=false;
          return this.getEnjoyProjects();
        });
    },
    //Echart
    mounted(){
      this.eChart(this.chartData.going,this.chartData.hold,this.chartData.reject);
    },
    components: {
      alertprojectdetail,
      addfollow,
      projectpush,
      projectpreview
    },
    watch : {
      dialogVisibleTag : function (e) {
        if (e) {
          this.$global.func.getWxProjectCategory();
          setTimeout(()=>{this.getWxProjectCategory();},200)
        }
      }
    }
  }
</script>

<style type="text/css" lang="less">
  @import '../../../assets/css/contactsDetails';
</style>



